<template>
  <div class="hello">
      <p v-if="predictionScore && !isLoading">NSFW Prediction: {{predictionScore}}</p>
      <div v-if="isLoading" class="lds-css ng-scope">
        <div style="width:100%;height:100%" class="lds-ripple"><div></div><div></div></div>
        Retrieving predictions
      </div>
      
      <picture-input 
        ref="pictureInput"
        width="600" 
        height="600" 
        margin="16" 
        accept="image/jpeg,image/png" 
        size="10" 
        button-class="btn"
        :custom-strings="{
          upload: '<h1>Bummer!</h1>',
          drag: 'Drag an image!'
        }"
        @change="onChange">
      </picture-input>

  </div>
</template>

<script>
import PictureInput from 'vue-picture-input'

export default {
  name: 'HelloWorld',
  data: function() {
    return {
      predictionScore: null,
      isLoading: false,
    }
  },
  components: {
    PictureInput
  },
  methods: {
    sendUploadToBackend(name, data) {
      self = this;
      self.isLoading = true;
      fetch('http://localhost:5000/api/v1/classify',
        {
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            method: "POST",
            body: JSON.stringify({'name': name, 'data': data})
        })
      .then(function(response) {
        return response.json();
      })
      .then(function(json) {
        self.predictionScore = json.nsfw;
        self.isLoading = false;
        console.log(JSON.stringify(json));
      })
      .catch(function(res){ 
        self.isLoading = false;
        console.log(res) 
      });
      console.log('tried code in sendUploadToBackend')
    },
    onChange(image) {
      console.log('New picture selected!')
      if (this.$refs.pictureInput.image) {
        console.log('Picture is loaded.')
        this.sendUploadToBackend(this.$refs.pictureInput.file.name, this.$refs.pictureInput.image)
      } else {
        console.log('FileReader API not supported: use the <form>, Luke!')
      }
    },
  }
}
</script>


